/*  边框动画  */
@keyframes rotate {
100% {
    transform: rotate(1turn);
}
}
.faq-item[data-v-37b5a63c] {
  position: relative;
  z-index: 0;
  overflow: hidden;
  padding: 10px;
}
.faq-item[data-v-37b5a63c]:before {
  content: '';
  position: absolute;
  z-index: -2;
  left: -50%;
  top: -50%;
  width: 200%;
  height: 200%;
  background-color: #1a232a;
  background-repeat: no-repeat;
  background-position: 0 0;
  background-image: conic-gradient(transparent, rgba(233, 157, 66, 1), transparent 30%);
}
.faq-item-1[data-v-37b5a63c]:before{
  animation: rotate 1.8s linear infinite;
}
.faq-item-2[data-v-37b5a63c]:before{
  animation: rotate 2.2s linear infinite;
}
.faq-item-3[data-v-37b5a63c]:before{
  animation: rotate 3.2s linear infinite;
}
.faq-item-4[data-v-37b5a63c]:before{
  animation: rotate 1.5s linear infinite;
}
.faq-item[data-v-37b5a63c]:after {
  content: '';
  position: absolute;
  z-index: -1;
  left: 3px;
  top: 3px;
  width: calc(100% - 6px);
  height: calc(100% - 6px);
  background: #000;
  border-radius: 15px;
}

/*标题发光样式*/
@keyframes Glow {
from {
    text-shadow: 0 0 10px #fff,
      0 0 20px #fff,
      0 0 30px #fff,
      0 0 40px #e99d42,
      0 0 70px #e99d42,
      0 0 80px #e99d42,
      0 0 100px #e99d42,
      0 0 150px #e99d42;
}
to {
    text-shadow: 0 0 5px #fff,
      0 0 10px #fff,
      0 0 15px #fff,
      0 0 20px #e99d42,
      0 0 35px #e99d42,
      0 0 40px #e99d42,
      0 0 50px #e99d42,
      0 0 75px #e99d42;
}
}


/*按钮样式*/
.button-docs[data-v-37b5a63c] {
  display: inline-block;
  width: 355px;
  height: 65px;
  margin: 30px 20px;
  background-color: #000000;
  color: #fff;
  border-radius: 60px;
  text-transform: uppercase;
  border: 2px solid #3b0e7b;
  font-size: 24px;
  cursor: pointer;
  position: relative;
  /*
  box-shadow: 0 2px 25px rgba(233, 30, 99, 0.5);
  */
  outline: 0;
  transition: transform ease-in 0.1s, background-color ease-in 0.1s,
    box-shadow ease-in 0.25s;
}
.button-docs[data-v-37b5a63c]::before {
  position: absolute;
  content: "";
  left: -2em;
  right: -2em;
  top: -2em;
  bottom: -2em;
  pointer-events: none;
  transition: ease-in-out 0.5s;
  background-repeat: no-repeat;
  /* 按钮泡沫效果 */
  background-image: radial-gradient(circle, #ff0081 20%, transparent 20%),
    radial-gradient(circle, #ff0081 20%, transparent 20%),
    radial-gradient(circle, #ff0081 20%, transparent 20%),
    radial-gradient(circle, #ff0081 20%, transparent 20%),
    radial-gradient(circle, #ff0081 20%, transparent 20%),
    radial-gradient(circle, #ff0081 20%, transparent 20%),
    radial-gradient(circle, #ff0081 20%, transparent 20%),
    radial-gradient(circle, #ff0081 20%, transparent 20%),
    radial-gradient(circle, #ff0081 20%, transparent 20%),
    radial-gradient(circle, #ff0081 20%, transparent 20%),
    radial-gradient(circle, #ff0081 20%, transparent 20%),
    radial-gradient(circle, #ff0081 20%, transparent 20%),
    radial-gradient(circle, #ff0081 20%, transparent 20%),
    radial-gradient(circle, #ff0081 20%, transparent 20%),
    radial-gradient(circle, #ff0081 20%, transparent 20%),
    radial-gradient(circle, #ff0081 20%, transparent 20%);
  background-size: 10% 10%, 20% 20%, 15% 15%, 20% 20%, 18% 18%, 10% 10%, 15% 15%,
    10% 10%, 18% 18%, 15% 15%, 20% 20%, 18% 18%, 20% 20%, 15% 15%, 10% 10%,
    20% 20%;
  background-position: 18% 40%, 20% 31%, 30% 30%, 40% 30%, 50% 30%, 57% 30%,
    65% 30%, 80% 32%, 15% 60%, 83% 60%, 18% 70%, 25% 70%, 41% 70%, 50% 70%,
    64% 70%, 80% 71%;
  animation: bubbles ease-in-out 0.75s forwards;
  -webkit-animation: bubbles ease-in-out 0.75s forwards;
  -o-animation: bubbles ease-in-out 0.75s forwards;
  -moz-animation: bubbles ease-in-out 0.75s forwards;
}
.button-docs[data-v-37b5a63c]:active {
  transform: scale(0.95);
  background-color: #f3037c;
  box-shadow: 0 2px 25px rgba(233, 30, 99, 0.5);
}
.button-docs[data-v-37b5a63c]:active::before {
  animation: none;
  background-size: 0;
}
@keyframes bubbles {
0% {
    background-position: 18% 40%, 20% 31%, 30% 30%, 40% 30%, 50% 30%, 57% 30%,
      65% 30%, 80% 32%, 15% 60%, 83% 60%, 18% 70%, 25% 70%, 41% 70%, 50% 70%,
      64% 70%, 80% 71%;
}
50% {
    background-position: 10% 44%, 0% 20%, 15% 5%, 30% 0%, 42% 0%, 62% -2%,
      75% 0%, 95% -2%, 0% 80%, 95% 55%, 7% 100%, 24% 100%, 41% 100%, 55% 95%,
      68% 96%, 95% 100%;
}
100% {
    background-position: 5% 44%, -5% 20%, 7% 5%, 23% 0%, 37% 0, 58% -2%, 80% 0%,
      100% -2%, -5% 80%, 100% 55%, 2% 100%, 23% 100%, 42% 100%, 60% 95%, 70% 96%,
